.notification-validate {
    color: red;
    margin: 0;
    font-size: small;
}
.modal {
    width: 100%;
    height: 100%;
    background-color: rgba(146, 0, 0, 0.562);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
    .modal-close {
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 101;
        cursor: pointer;
    }
    .modal-children {
        z-index: 102;
        background-color: #fff;
        padding: 20px;
    }
}
.modal-none {
    display: none;
}
.new-blog {
    max-width: 1200px;
    width: 100%;
    h1 {
        text-align: center;
    }
    .input-title {
        margin-bottom: 20px;
    }
    .btn-create-new-blog {
        margin-top: 20px;
        margin-bottom: 50px;
        display: flex;
        justify-content: flex-end;
    }
}
